*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}
html,body{
    
    width: 100%;
    height: 100%;
}
.header{
    width: 100%;
    height: 1.2rem;
    background-color:#91A0D7;
}
.admin-div{
    position: absolute;
    top: .15625rem;
    right: .833333rem;
}
.admin-svg{
   
    font-size: .416667rem;
}
.admin-span{
    position: absolute;
    top: .416667rem;
    left: 50%;
    transform: translateX(-50%);
    font-family: 微软雅黑,宋体,Arial,Helvetica,Verdana,sans-serif;
}
.admin-btn{
    position: absolute;
    z-index: 10000;
    top: 1.2rem;
    right:.25rem;
    text-decoration:none;
	background:#2f435e;
	color:#f2f2f2;
	outline: none;
	padding: 5px 15px 5px 15px;
	font-size:16px;
	font-family: 微软雅黑,宋体,Arial,Helvetica,Verdana,sans-serif;
	font-weight:bold;
	border-radius:3px;
	
	-webkit-transition:all linear 0.30s;
	-moz-transition:all linear 0.30s;
	transition:all linear 0.30s;
}